/* pc */
/* Ipad */
@media only screen and (min-width: 701px) {
   .header_wrapper {
      justify-content: space-between;
      margin-right: 1%;
    margin-left: 1%;

   }}
/* iphone */
@media only screen and (max-width: 700px){
   .header_wrapper { margin-left: 2%}
}

 .header_wrapper {
   display: flex;
   align-items: center;
   height: 100%
 }
/* LOGO */
/* pc */
/* Ipad */
@media only screen and (min-width: 701px){
   .header_logo {
      width: 5%
   }
}

/* iphone */
@media only screen and (max-width: 700px){
   .header_logo {
      width: 10%;
   }}

   .header_logo > img {
      width: 100%;
   }

/* SEARCH */
 /* pc */
/* Ipad */
@media only screen and (min-width: 701px){
   .header_search {
      width: 45%;
      height: 30%;}
.header_searchContainer {
   border: 1px solid #31363A;
   border-radius: 4px;
}
.header_searchContainer > img {
   padding: 0.6%;
}
.Search_Button_Modal{ display: none;
}
}

/* iphone */
@media only screen and (max-width: 700px){
   .header_search {
      width: 15%;
      height: 100%
}
.header_searchContainer{ justify-content: center;}
.header_searchContainer > img{display: none;}
.header_searchContainer > input{display: none;}
}

.Search_Button_Modal > img {
   width: 100%
}
.Search_Button_Modal {
   background-color: black;
   border: none;
   width: 70%
}
 .header_search {
   padding: 1%;
   align-items: center;
}

 .header_searchContainer {
   height: 100%;
   width: 100%;
   display: flex;
 }
 
 .header_searchContainer > input {
   background-color: transparent;
   border: none;
   width: 90%;
   margin-left: 5%;
   color: white;

 }
 .header_searchContainer > input:focus {
   outline: none
 }


 /* MENU */
 /* pc */
 @media only screen and (min-width: 1201px) {
   .header_menuItems {
      width: 50%;
   }
 }
 /* Ipad */
 @media only screen and (min-width: 701px) and (max-width: 1200px){
   .header_menuItems {
      font-size: 80%;
      width: 50%;
   }
   .header_menuItems > c {
      padding-left: 2%;
   }
   .header_menuItems > b {
      padding-left: 2%;
      padding-right: 2%
   }
   .header_menuItems > a {
      padding-right: 5%;
   }
   
}
 /* iphone */
 @media only screen and (max-width: 700px) {
   .header_menuItems {
      font-size: 50%;
      width: 78%;
 }
}
 .header_menuItems {
   display: flex;
    justify-content: space-around;
 }
 .header_menuItems > a {
   color: white;
    font-weight: bold;
    text-decoration: none;
    padding-left: 1%;
    white-space: nowrap;
 }
 .header_menuItems > b {
   color: white;
    font-weight: bold;
    text-decoration: none;
 }
 .header_menuItems > c {
   color: white;
    font-weight: bold;
    text-decoration: none;
    padding-right: 1%
   
 }
 




